"use client";

import { useState, useCallback } from "react";
import { JarvisChat } from "@/components/JarvisChat";
import { ChatInput } from "@/components/ChatInput";
import { callJarvisAPI } from "@/lib/jarvisAPI";

interface Message {
  role: "user" | "assistant";
  content: string;
  timestamp: Date;
}

export default function JarvisPage() {
  const [messages, setMessages] = useState<Message[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleSendMessage = useCallback(async (message: string) => {
    if (!message.trim() || isLoading) return;

    const userMessage: Message = {
      role: "user",
      content: message,
      timestamp: new Date(),
    };

    setMessages(prev => [...prev, userMessage]);
    setIsLoading(true);
    setError(null);

    try {
      // Prepare messages for API (without timestamps)
      const apiMessages = [...messages, userMessage].map(msg => ({
        role: msg.role,
        content: msg.content,
      }));

      const response = await callJarvisAPI(apiMessages);
      
      const assistantMessage: Message = {
        role: "assistant",
        content: response,
        timestamp: new Date(),
      };

      setMessages(prev => [...prev, assistantMessage]);
    } catch (error) {
      console.error("Error fetching JARVIS response:", error);
      
      const errorMessage = error instanceof Error ? error.message : "An unexpected error occurred";
      setError(errorMessage);
      
      const errorResponse: Message = {
        role: "assistant",
        content: `I apologize, but I encountered an error: ${errorMessage}. Please try again or check your API configuration.`,
        timestamp: new Date(),
      };

      setMessages(prev => [...prev, errorResponse]);
    } finally {
      setIsLoading(false);
    }
  }, [messages, isLoading]);

  const clearChat = () => {
    setMessages([]);
    setError(null);
  };

  return (
    <div className="flex flex-col h-screen">
      {error && (
        <div className="bg-destructive/10 border border-destructive/20 text-destructive px-4 py-2 text-sm">
          <div className="flex items-center justify-between">
            <span>Error: {error}</span>
            <button
              onClick={() => setError(null)}
              className="text-destructive hover:text-destructive/80"
            >
              Dismiss
            </button>
          </div>
        </div>
      )}
      
      <div className="flex-1 flex flex-col min-h-0">
        <JarvisChat messages={messages} isLoading={isLoading} />
        <ChatInput onSend={handleSendMessage} disabled={isLoading} />
      </div>
      
      {messages.length > 0 && (
        <div className="border-t border-border bg-card/30 px-4 py-2">
          <div className="flex items-center justify-between text-xs text-muted-foreground">
            <span>{messages.length} messages</span>
            <button
              onClick={clearChat}
              className="hover:text-foreground transition-colors"
              disabled={isLoading}
            >
              Clear Chat
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
